<template>
  <div>
    <Overlay :isOpen="isOpen" @close="isOpen = false">
      <template slot="header">
        Formulário teste
      </template>
      <div class="form">
        <div class="form-group">
          <label>Assign responsibility</label>
          <label>
            <Checkbox :checked="a" @click="a = !a" />Gilad gray</label>
          <label>
            <Checkbox :checked="b" @click="b = !b" />Jason Killian</label>
          <label>
            <Checkbox :checked="c" @click="c = !c" />Antoine Llorca</label>
        </div>
        <div class="form-group">
