<script>
export default {
  name: "ordem-de-servico",
  data() {
    return {
      items: [
        { id: 1, text: "diego" },
        { id: 2, text: "braga" },
        { id: 3, text: "guedes" }
      ],
      isOpen: true,
      a: true,
      b: true,
      c: true,
      kk2: null,
      kk3: null,
      kk4: [],
      kk5: null,
      kk6: null,
      data: Array.from({ length: 30 }, (v, k) => ({
        id: k,
        teste1: k.toString(),
        teste2: k.toString(),
        teste3: k.toString(),
        projeto: k.toString(),
        cliente: new Date()
      })),
      columns: [
        {
          label: "Projeto",
          field: "projeto",
          type: "text",
          visible: true
        },
        {
          label: "Teste1",
          field: "teste1",
          type: "text",
          visible: true
        },
        {
          label: "Teste2",
          field: "teste",
          type: "text",
          visible: true
        },
        {
          label: "Teste3",
          field: "teste3",
          type: "text",
          visible: true
        },
        {
          label: "Teste3",
          field: "teste3",
          type: "text",
          visible: true
        },
        {
          label: "Cliente",
          field: "cliente",
          type: "date",
          visible: true
        }
      ]
    };
